<template>
  <div
    ref="textContainer"
    :style="textStyle"
    class="info-text"
    :class="{'center-text': centerText,
              'border-text': isWrapperBorder,
              'underImage-text': isUnderImage}"
  >
    <div ref="textContent" v-html="contentText"></div>
  </div>
</template>

<script>
export default {
  name: 'InfoText',
  props: {
    contentText: String,
    InternalID: Number,
    centerText: Boolean,
    isWrapperBorder: Boolean,
    isUnderImage: Boolean
  },
  data() {
    return {
      totalLines: 0
    }
  },
  mounted() {
    this.calculateTotalLines();
    window.addEventListener('resize', this.calculateTotalLines);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.calculateTotalLines);
  },
  watch: {
    contentText() {
      this.calculateTotalLines();
    }
  },
  computed: {
    textStyle() {
      return this.getTextStyle(this.totalLines);
    }
  },
  methods: {
    calculateTotalLines() {
      setTimeout(() => {
        const textContent = this.$refs.textContent;
        if (textContent) {
          const totalHeight = textContent.clientHeight;
          const lineHeight = parseInt(window.getComputedStyle(textContent).lineHeight, 10);
          const lines = Math.round(totalHeight / lineHeight);
          this.totalLines = lines;
        }
      }, 0);
    },
    getTextStyle(totalLines) {
      console.log("totalLines: " + totalLines)
      if (totalLines > 4 && this.isWrapperBorder) {
        return {
          fontSize: '20px',
          lineHeight: '24px'
        }
      } else if (totalLines > 17 && !this.centerText) {
        return {
          fontSize: '26px',
          lineHeight: '30px',
          paddingBottom: '7vh'
        }
      } else if (totalLines <= 15 && totalLines > 6 && this.centerText) {
        return {
          width: '60vw',
          fontSize: '24px',
          lineHeight: '28px',
          marginTop: '-5vh'
        }
      } else if (totalLines > 15 && this.centerText) {
        return {
          width: '60vw',
          fontSize: '20px',
          lineHeight: '24px',
          marginTop: '-5vh'
        }
      } else if (totalLines > 4 && totalLines < 15 &&!this.centerText) {
        return {
          paddingBottom: '10vh'
        }
      } else if (totalLines < 17 && !this.centerText && this.isWrapperBorder) {
        return {
          paddingBottom: '0vh'
        }
      }
    }
  }
}
</script>
  
